	module.exports = {"data":{"markdownRemark":{"html":"<h2>Scrum</h2>\n<p>Scrum is one of the methodologies under the Agile umbrella. It originates from ‘scrummage’ which is a term used in rugby to denote players huddling together to get possession of the ball.\nThe practice revolves around </p>\n<ul>\n<li>A set of roles (delivery team, product owner, and scrum master)</li>\n<li>Ceremonies (sprint planning, daily standup, sprint review, sprint retrospective, and backlog grooming)</li>\n<li>Artifacts (product backlog, sprint backlog, product increment, and info radiators and reports).</li>\n<li>The main goal is to keep the team aligned on project progress to facilitate rapid iteration.</li>\n</ul>\n<h4>More Information:</h4>\n<p>There are several online tools that can be used to do scrum for your team. e.g. <a href='https://www.scrumdo.com/'>Scrum Do</a>, <a href='http://www.asana.com'>Asana</a>, or <a href='http://trello.com'>Trello</a></p>\n<p><a href='https://www.scrumalliance.org/why-scrum'> Why Scrum </a> from The Scrum Alliance.</p>\n<p><a href='http://agilitrix.com/2016/04/doing-agile-vs-being-agile/'>Doing vs Being Agile</a></p>","fields":{"slug":"/agile/scrum/"},"frontmatter":{"title":"Scrum"}}},"pathContext":{"slug":"/agile/scrum/"}}

